@import "../../styles/shared";

#contact {
  padding-top: 1rem;
  background-color: $reverse-font-color;
}

.contact {
  padding: 50px 150px;
  color: $base-font-color;
}
.contact input,
textarea {
  color: black;
}

.contact-header-quote {
  font-style: italic;
}
.contact-header-quote h4 {
  margin: 5px 0;
}
.contact-instructions,
.contact-form-body {
  margin-top: 50px;
  padding: 0px 6%;
}

.email,
.message {
  margin: 15px 0px;
}
.contact input,
.contact textarea {
  width: 100%;
  padding: 10px;
  margin: 3px 0px;
}

.submit-contact .btn-border {
  margin-top: 10px;
}

.flex-contact {
  width: 100%;
  margin: 0 auto;
  display: flex;
}
.flex-contact-item {
  flex: 0 0 50%;
}

@media (max-width: 750px) {
  .flex-contact {
    flex-direction: column;
  }

  .contact {
    padding: unset;
    padding-bottom: 5rem;
  }
}

.contact-header,
.contact-header-quote {
  text-align: center;
}
